@font-face {
    font-family: zabal;
    font-weight: 400;
    src: url(zabal/ZabalDEMO-Regular.otf);
  }
@font-face {
    font-family: zabal;
    font-weight: 500;
    src: url(zabal/ZabalDEMO-Medium.otf);
  }
@font-face {
    font-family: zabal;
    font-weight: 700;
    src: url(zabal/ZabalDEMO-Bold.otf);
  }
@font-face {
    font-family: zabal;
    font-weight: 800;
    src: url(zabal/ZabalDEMO-ExtraBold.otf);
  }
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: zabal;
}

/* global starts */
:root {
    --cs-titleblack: #212121;
    --cs-primary:#EE6611;
    --cs-white: #fff;
    --cs-bgblack: #353535;
    --cs-bgwhite: #F5F5F5;
    --cs-sky: #ECF6FF;
    --cs-span: #1679D4;
    --cs-team: #FCDDCA;
}
h1{
    font-size: 48px;
    font-weight: 800;
    line-height: 59px;
}
h2{
    font-size: 40px;
    font-weight: 700;
    line-height: 48px;
}
h4 {
    font-size: 24px;
    font-weight: 500;
    line-height: 30px;
}

a {
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;
    text-decoration: none;
}
p{
    font-weight: 400;
    font-size: 20px;
}
a:hover{
    color: var(--cs-primary);
}
ul{
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
.cs-text {
    color: #EE6611 !important;
}
.cs-btn{
    background: #EE6611 !important;
    border-radius: 8px;
    font-family: zabal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #FFFFFF;
    border: 2px solid #EE6611 !important;
    padding: 12px 24px;
    transition: .3s;
}
.cs-btn-about{
    background: #FFFFFF !important;
    border-radius: 8px;
    font-family: zabal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #EE6611 !important;
    border: 2px solid #EE6611 !important;
    padding: 12px 24px;
    transition: .3s;
}
.cs-btn-software{
    background: #EE6611 !important;
    border-radius: 8px;
    font-family: zabal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #FFFFFF;
    border: 2px solid #EE6611 !important;
    padding: 20px 24px;
    transition: .3s;
}

@media (max-width: 900px)  {
    .cs-btn-about{
        background: #FFFFFF !important;
        border-radius: 8px;
        font-family: zabal;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
        color: #EE6611 !important;
        border: 2px solid #EE6611 !important;
        padding: 12px 24px;
        transition: .3s;
        margin-top: 20px;
    }
}
.cs-btn-secondary{
    background: #fff !important;
    color: #EE6611 !important;
    border: 2px solid #EE6611 !important;
    transition: .3s;
}
.cs-btn:hover {
    background: #d1590e !important;
    transition: .3s;
}
.cs-btn-about:hover {
    background: #d1590e !important;
    transition: .3s;
    color: #fff !important;
}
.cs-btn-secondary:hover {
    color: #fff !important;
    background: #EE6611 !important;
    transition: .3s;
}
img {
    border-radius: 4px;
}
/* ------------global ends---------- */
/* header starts */
header {
    background: var(--cs-white);
    
}
.header{
    background: var(--cs-white);
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    padding: 15px 0;
}
.navbar-brand {
    max-width: 108px;
    margin-left: 20px;
}
@media (max-width: 992px) {
    .navbar-brand {
        width: 108px;
        margin-left: 20px;
    }
}

.navLinkContainer{
    margin-right: 20px;
}
.navbar-toggler:focus {
    text-decoration: none;
    outline: 0;
    box-shadow: 0 0 0 0;
    background-color: #9fccef;
}
.navbar-toggler i{
    color: var(--cs-titleblack);
    font-size: 25px;
}
.nav-item {
    padding: 5px 8px;
}
.nav-link{
font-family: Zabal;
font-size: 20px;
font-weight: 400;
line-height: 24px;
letter-spacing: 0em;
text-align: left;
color: var(--cs-titleblack);
position: relative;

}
.nav-link:focus,
.nav-link:hover{
    color: rgb(238 102 17);
}
.nav-link.active{
    color: rgb(238 102 17);
}
 .nav-link::before {
    content: "";
    position: absolute;
    background-color: var(--cs-primary);
    left: 0px;
    bottom: -6px;
    width: 0px;
    height: 3px;
    transition: all 0.3s ease-in-out 0s;
    z-index: 1;
}
.active::before {
    content: "";
    position: absolute;
    background-color: var(--cs-primary);
    left: 0px;
    bottom: -6px;
    width: 100%;
    height: 3px;
    transition: all 0.3s ease-in-out 0s;
    z-index: 1;
}
 .nav-link:hover::before {
    width: 100%;
  }


.nav-btn {
    margin-left: 20px;
}

.btn,
.btn:focus,
.btn:hover {
    color: var(--cs-white);
    background: var(--cs-primary);
    outline: 0;
    box-shadow: none;
}
/* header ends  */
